import React from "react";
import styled from "styled-components";
import { Link } from "gatsby";

const HeaderContainer = styled.header`
  background: #f7f7f7;
  display: flex;
  justify-content: space-between;
  padding: 10px calc((100vw - 600px) / 2);
`;

const NavLink = styled(Link)`
  color: #222;
  font-size: 16px;
  line-height: 1;
  ${({ fontWeight }) => fontWeight && `font-weight: ${fontWeight};`}
  padding: 4px;
  margin-right: 10px;
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }

  &.current-page {
    border-bottom: 2px solid #222;
  }
  &:last-child {
    margin-right: 0;
  }
`;

const Header = () => (
  <HeaderContainer>
    <NavLink to="/" fontWeight="bold">
      Gatsby Workshop
    </NavLink>
    <nav>
      <NavLink activeClassName="current-page" to="/">
        
      </NavLink>
      <NavLink activeClassName="current-page" to="/about/">
        
      </NavLink>
      <NavLink activeClassName="current-page" to="/contact/">
        
      </NavLink>
      <NavLink activeClassName="current-page" to="/buildings/">
        
      </NavLink>
    </nav>
  </HeaderContainer>
);

export default Header;
